import { FILTER_ROOMS, GET_ROOMS, SORT_ROOMS } from "./actionType";

export const getData = (data) => {
    return {
      type: GET_ROOMS,
      payload: data,
    };
  };
  
  export const filterData = (data) => {
    return {
      type: FILTER_ROOMS,
      payload: data,
    };
  };
  
  export const sortData = (data) => {
    return {
      type: SORT_ROOMS,
      payload: data,
    };
  };